import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { finallogo, sun } from '../assets';
import { navlinks } from '../constants';

const Icon = ({ styles, name, imgUrl, isActive, disabled, handleClick }) => (
  <div
    className={`w-[48px] h-[48px] rounded-[50px] flex justify-center items-center ${
      !disabled && 'cursor-pointer'
    } ${styles} hover:bg-[#2c2f32] hover:rounded-[25%]`}
    onClick={handleClick}
  >
    {!isActive ? (
      <img src={imgUrl} alt={name} className={`w-1/2 h-1/2 `} />
    ) : (
      <img
        src={imgUrl}
        alt={name}
        className={`w-1/2 h-1/2 ${isActive !== name && 'grayscale'}`}
      />
    )}
  </div>
);

const SideBar = () => {
  const navigate = useNavigate();
  const [isActive, setIsActive] = useState('dashboard');
  return (
    <div className="flex justify-between items-center flex-col sticky top-5 h-[93vh]">
      <Link to="/">
        <div className="h-12 w-12">
          <img src={finallogo} />
        </div>
      </Link>

      <div className="flex-1 flex flex-col justify-between items-center bg-[#44403C] rounded-[20px] w-[76px] py-4 mt-12">
        <div className="flex flex-col justify-center items-center gap-3">
          {navlinks.map((link) => (
            <Icon
              key={link.name}
              {...link}
              isActive={isActive}
              handleClick={() => {
                if (!link.disabled) {
                  setIsActive(link.name);
                  navigate(link.link);
                }
              }}
            />
          ))}
        </div>
        <Icon styles="shadow-secondary bg-[#1c1c24]" imgUrl={sun} />
      </div>
    </div>
  );
};

export default SideBar;
